/* home-container to wrap the entire layout */
  .home-container {
    display: flex;
    flex-direction: column;
    min-width: 100vw; /* Ensure the footer stays at the bottom of the page */
    min-height: 100vh; /* Ensure the footer stays at the bottom of the page */
  }
  
  /* Header styles */
   .header {
    display: flex;
    flex-direction: column;
    background-color: #333;
    color: white;
    text-align: center;
    padding: 7vh 0 4vh;
  }

  #home-header {
    padding: 7vh 0 0;
    display: flex;
    flex-direction: column;
  }

  .header .header-title {
    padding-bottom: 5vh;
  }

  .header .image-header-container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    position: relative;
    overflow: hidden; /* Evita che l'immagine sfocata fuoriesca dal contenitore */
}

.header .image-header-container::before {
    content: ''; /* Crea un pseudo-elemento */
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../assets/pac-man.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    filter: blur(3px); /* Imposta il livello di sfocatura desiderato */
    z-index: 0; /* Imposta il filtro dietro al testo, ma sopra il contenitore */
}


.header .text-content {
  position: relative; /* Mantiene il testo sopra l'overlay */
  z-index: 1; /* Assicura che il testo sia sopra l'immagine */
  color: white; /* Imposta il colore del testo a piacere */
  padding: 10vh;
  background: #0c0b0bb2;
  text-shadow: 
      2px 2px 5px rgba(0, 0, 0, 0.836), /* Ombra principale */
      -2px -2px 5px rgba(24, 71, 172, 0.986); /* Ombra di rilievo per un effetto di profondità */
}



  .header .image-header-container .dog-image {
    display: flex;
    flex-direction: column;
    width: 100vw;
  }
    .main {
      flex-grow: 1; 
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 5vh 2vmin;
    }
  
  .header h1 {
    font-size: 7vmin;
    margin: 0;
  }
  
  /* Body styles */
  .body {
    flex-grow: 1; 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5vh 2vmin;
  }
  
  .App-logo {
    width: 150px; 
    margin-bottom: 20px;
  }
  
  .body p {
    font-size: 1.2rem;
    color: #333;
    text-align: center;
  }


  .youtube-music  iframe{
    border-radius: 30px;
  }
  

  footer{
    background: linear-gradient(150deg, #0f3138, #334347);
    width: 100%;
    bottom: 0;
    left: 0;
  }
  
  
  footer .logo {
    color: white;
    font-size: 4vmin; /* Dimensione logo dinamica */
    font-weight: bold;
    text-transform: uppercase;
    margin-left: 2vw; /* Usa unità relative per il margine */
    display: flex;
    align-items: center;
  }

  footer .logo .footer-logo {
    height: auto;
    width:25vmin; /* Usa una larghezza dinamica in base alla viewport */
    max-width: 100%; /* Impedisce che l'immagine ecceda la larghezza del contenitore */
    max-height: 8vh; /* Limita l'altezza dell'immagine */
    border-radius: 100px;
  }

  footer::before{
    content: '';
    left: 0;
    top: 100px;
    height: 1px;
    width: 100%;
    background: #0f3138;
  }
  footer .content{
    max-width: 100%;
    margin: auto;
    padding: 30px 40px 40px 40px;
  }
  footer .content .top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
  }
  .content .top .logo-details{
    color: #fff;
    font-size: 30px;
  }
  .content .top .media-icons{
    display: flex;
  }
  .content .top .media-icons a{
    height: 40px;
    width: 40px;
    margin: 0 8px;
    border-radius: 50%;
    text-align: center;
    line-height: 40px;
    color: #fff;
    font-size: 17px;
    text-decoration: none;
    transition: all 0.4s ease;
  }
  .top .media-icons a:nth-child(1){
    background: #161f30;
    transition: transform 0.2s ease-in-out; /* Aggiungi una transizione morbida */
}

.top .media-icons a:nth-child(1):hover{
    color: #000;
    background: #ffffff;
    transform: scale(1.1); /* Ingrandisce l'elemento al 110% */
}

.top .media-icons a:nth-child(2){
    background: #446ef7;
    transition: transform 0.2s ease-in-out; /* Aggiungi una transizione morbida */
}

.top .media-icons a:nth-child(2):hover{
    color: #5376eb;
    background: #ffffff;
    transform: scale(1.1); /* Ingrandisce l'elemento al 110% */
}

.top .media-icons a:nth-child(3){
    background: #1DA1F2;
    transition: transform 0.2s ease-in-out; /* Aggiungi una transizione morbida */
}

.top .media-icons a:nth-child(3):hover{
    color: #1DA1F2;
    background: #fff;
    transform: scale(1.1); /* Ingrandisce l'elemento al 110% */
}

.top .media-icons a:nth-child(4){
    background: #E1306C;
    transition: transform 0.2s ease-in-out; /* Aggiungi una transizione morbida */
}

.top .media-icons a:nth-child(4):hover{
    color: #E1306C;
    background: #fff;
    transform: scale(1.1); /* Ingrandisce l'elemento al 110% */
}

.top .media-icons a:nth-child(5){
    background: #ec270d;
    transition: transform 0.2s ease-in-out; /* Aggiungi una transizione morbida */
}

.top .media-icons a:nth-child(5):hover{
    color: #ec270d;
    background: #fff;
    transform: scale(1.1); /* Ingrandisce l'elemento al 110% */
}

.top .media-icons a:nth-child(6){
    background: #FF0000;
    transition: transform 0.2s ease-in-out; /* Aggiungi una transizione morbida */
}

.top .media-icons a:nth-child(6):hover{
    color: #FF0000;
    background: #fff;
    transform: scale(1.1); /* Ingrandisce l'elemento al 110% */
}

  footer .content .link-boxes{
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  footer .content .link-boxes .box{
    width: calc(100% / 5 - 10px);
  }
  .content .link-boxes .box .link_name{
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
    position: relative;
  }
  .link-boxes .box .link_name::before{
    position: absolute;
    left: 0;
    bottom: -2px;
    height: 2px;
    width: 35px;
    background: #fff;
  }
  .content .link-boxes .box li{
    left: 0;
    margin: 6px 0;
    list-style: none;
  }
  .content .link-boxes .box li a{
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    text-decoration: none;
    opacity: 0.8;
    transition: all 0.4s ease
  }
  .content .link-boxes .box li a:hover{
    opacity: 1;
    font-weight: bold;
    color: #13e2e2;
  }
  .content .link-boxes .input-box{
    margin-right: 55px;
  }
  .link-boxes .input-box input{
    height: 40px;
    width: calc(100% + 55px);
    outline: none;
    border: 2px solid #AFAFB6;
    background: #140B5C;
    border-radius: 4px;
    padding: 0 15px;
    font-size: 15px;
    color: #fff;
    margin-top: 5px;
  }
  .link-boxes .input-box input::placeholder{
    color: #AFAFB6;
    font-size: 16px;
  }
  .link-boxes .input-box input[type="button"]{
    background: #fff;
    color: #0f3138;
    border: none;
    font-size: 18px;
    font-weight: 500;
    margin: 4px 0;
    opacity: 0.8;
    cursor: pointer;
    transition: all 0.4s ease;
  }
  .input-box input[type="button"]:hover{
    opacity: 1;
  }
  footer .bottom-details{
    width: 100%;
    background: linear-gradient(10deg, #0f3138, #334347);
  }
  footer .bottom-details .bottom_text{
    max-width: 1250px;
    margin: auto;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
  }
  .bottom-details .bottom_text span,
  .bottom-details .bottom_text a{
    font-size: 14px;
    font-weight: 300;
    color: #fff;
    opacity: 0.8;
    text-decoration: none;
  }
  .bottom-details .bottom_text a:hover{
    opacity: 1;
    text-decoration: underline;
  }
  .bottom-details .bottom_text a{
    margin-right: 10px;
  }
  @media (max-width: 900px) {
    footer .content .link-boxes{
      flex-wrap: wrap;
    }
    footer .content .link-boxes .input-box{
      width: 40%;
      margin-top: 10px;
    }
  }
  @media (max-width: 700px){
    footer{
      position: relative;
    }
    .content .top .logo-details{
      font-size: 26px;
    }
    .content .top .media-icons a{
      height: 35px;
      width: 35px;
      font-size: 14px;
      line-height: 35px;
    }
    footer .content .link-boxes .box{
      width: calc(100% / 3 - 10px);
    }
    footer .content .link-boxes .input-box{
      width: 60%;
    }
    .bottom-details .bottom_text span,
    .bottom-details .bottom_text a{
      font-size: 12px;
    }
  }
  @media (max-width: 520px){
    footer::before{
      top: 145px;
    }
    footer .content .top{
      flex-direction: column;
    }
    .content .top .media-icons{
      margin-top: 16px;
    }
    footer .content .link-boxes .box{
      width: calc(100% / 2 - 10px);
    }
    footer .content .link-boxes .input-box{
      width: 100%;
    }
  }
