  /*main Style*/

  .main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin: 2rem;
  }
  
  .main .main-title {
    font-size: 2rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 2rem;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .main .content-container {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
    width: 100%;
  }
  

  .main #first-section  { 
    flex: 1 1 calc(100% - 2rem);
  }


  .main .section {
    flex: 1 1 calc(30% - 2rem);
    background-color: #f4f4f4;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .main .section:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
  }
  
  .main .section-title {
    font-size: 1.5rem;
    font-weight: bold;
    color: #1e2935;
    margin-bottom: 1rem;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .main .section p {
    font-size: 1rem;
    color: #505657;
    line-height: 1.6;
    text-align: justify;
  }
  
  @media (max-width: 768px) {
    .main .section {
      flex: 1 1 calc(50% - 2rem);
    }
  }
  
  @media (max-width: 480px) {
    .main .section {
      flex: 1 1 100%;
    }
  }
  